import React, { Component } from "react";
import HeaderCard from "../HeaderCard";
import CetakBarcodeHead from "./CetakBarcodeHead";

class CetakBarcode extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Cetak Barcode" />
          <div className="card-body">
            <div className="row">
              <div className="col-lg-12">
                {/* BARCODE 1 */}
                <CetakBarcodeHead />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default CetakBarcode;
